<template>
  <v-card>
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title>CHAT: {{ username }}</v-list-item-title>
        <v-list-item-title>VIEWER: {{ chatters }}</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
  </v-card>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    username: "",
    chatters: "",
  }),
  methods: {
    async getUpdate() {
      let username = localStorage.getItem("username");
      let uri = `https://bejewled-understood-clove.glitch.me/chatters/${username}`;
      try {
        const response = await axios.get(uri);
        this.username = localStorage.getItem("username").toUpperCase();
        this.chatters = response.data.chatter_count;
      } catch (error) {
        this.chatters = "API ERROR";
      }
    },
  },
  async created() {
    let username = localStorage.getItem("username");
    let uri = `https://bejewled-understood-clove.glitch.me/chatters/${username}`;
    try {
      const response = await axios.get(uri);
      this.username = localStorage.getItem("username").toUpperCase();
      this.chatters = response.data.chatter_count;
    } catch (error) {
      this.chatters = "API ERROR";
    }
  },
  mounted() {
    setInterval(() => {
      this.getUpdate();
    }, 20000);
  },
};
</script>

