<template>
  <div>
    <div v-for="alert in this.alerts" v-bind:key="alert.message">
      <v-alert
        :value="alert.toggle"
        :color="alert.color"
        dark
        dismissible
        border="top"
        transition="scale-transition"
      >
        <h3 class="text-h5">{{ alert.type }}</h3>
        <div v-html="alert.message"></div>
      </v-alert>
    </div>
  </div>
</template>

<script>
import tmi from "tmi.js";
export default {
  data: () => ({
    alerts: [],
    message: "",
    username: "",
  }),
  methods: {
    genAlert(type, message, color) {
      let constructor = {
        toggle: true,
        type: type,
        message: message,
        color: color,
      };
      let alerts = this.alerts;
      return alerts.push(constructor);
    },
  },
  created() {
    this.username = localStorage.getItem("username");
    const opts = {
      channels: [this.username],
    };
    var client = new tmi.client(opts);

    client.connect();

    client.on(
      "subscription",
      (channel, username, method, message, userstate) => {
        this.genAlert("SUB", userstate["system-msg"], "red");
      }
    );

    client.on(
      "subgift",
      (channel, username, streakMonths, recipient, methods, userstate) => {
        this.genAlert("SUBGIFT", userstate["system-msg"], "red");
      }
    );

    client.on("cheer", (channel, userstate, message) => {
      let msg = `${userstate["display-name"]} just cheered ${userstate.bits}`;
      console.log(channel, userstate, message);
      if (!message == "") {
        if (message.includes("cheer")) {
          for (let index = 1; index < 100000; index++) {
            message = message.replace("cheer" + index, "");
          }
          msg = `${userstate["display-name"]} just cheered ${userstate.bits} bits.<br>Message: ${message}`;
        }
      }
      this.genAlert("CHEER", msg, "red");
    });

    client.on(
      "resub",
      (channel, username, months, message, userstate, methods) => {
        this.genAlert("RESUB", userstate["system-msg"], "red");
      }
    );

    client.on("raided", (channel, username, viewers) => {
      this.genAlert(
        "RAID",
        `${username} just raided ${channel.replace(
          "#",
          ""
        )} with ${viewers} viewers!`,
        "red"
      );
    });
    
  },
};
</script>